import React from "react";

class Landing extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      win: null,
    };
  }

  handleClick() {
    let params = `width=600,height=600,left=100,top=100`;

    this.setState({
      win: window.open(
        "http://localhost:5000/auth/facebook",
        "FaceBookAuth",
        params
      ),
    });
  }

  handleDemo() {
    console.log("Hiiiiiii");
    console.log("mai chal gyaa yooohooo");
    this.state.win.close();
  }
  render() {
    return (
      <div>
        <button onClick={this.handleDemo} id="demo"></button>
        <button onClick={this.handleClick}> Login with facebook</button>
      </div>
    );
  }
}

export default Landing;
